import React from 'react';
import cn from 'classnames';
import {useNavigate} from 'react-router-dom';

import {Button} from 'shared/ui';
import {bem} from 'shared/bem';

import './auth-buttons-group.scss';

const b = bem('auth-buttons-group');

type Props = {
  onClose?: () => void;
  className?: string;
};

export const AuthButtonsGroup = ({className, onClose}: Props) => {
  const navigate = useNavigate();

  const handleRedirectToLogin = () => {
    if (onClose) onClose();
    navigate('/login');
  };

  const handleRedirectToSignUp = () => {
    if (onClose) onClose();
    navigate('/signup');
  };

  const clsBtn = cn(b('btn'), className);

  return (
    <div className={b('btn-group')}>
      <Button onClick={handleRedirectToLogin} className={clsBtn}>
        Login
      </Button>
      <Button onClick={handleRedirectToSignUp} className={clsBtn}>
        Sign Up
      </Button>
    </div>
  );
};
