import React from 'react';

import {bem} from 'shared/bem';

import {Item} from './item';
import {navPersonItems} from './consts';
import './sidebar.scss';

const b = bem('sidebar');

export const Sidebar = () => {
  return (
    <div className={b()}>
      {navPersonItems.map((item) => (
        <Item key={item.link} item={item} />
      ))}
    </div>
  );
};
