import React, {useState, useCallback, ReactNode} from 'react';

import {useOutsideClick} from 'shared/hooks/use-outside-click';
import {ReactComponent as ChevronIcon} from 'assets/ic_chevron.svg';

import {b} from './consts';

import './dropdown.scss';

type Props<T> = {
  label?: string;
  disabled?: boolean;
  icon?: ReactNode;
  onClick?: () => void;
  onOptionClick?: (value?: T) => void;
  children: React.ReactNode;
};

export function Dropdown<T>({label, icon, onClick, onOptionClick, children}: Props<T>) {
  const [toggled, setToggled] = useState<boolean>(false);

  const handleToggle = useCallback(() => {
    setToggled((s) => !s);
  }, []);

  const handleClose = useCallback(() => {
    setToggled(false);
  }, []);

  const handleClick = useCallback(() => {
    if (onClick) {
      onClick();
    }
    handleClose();
  }, [onClick, handleClose]);

  /* const handleOptionClick = useCallback(
    (option: Option<T>) => {
      if (onOptionClick) {
        onOptionClick(option.value);
      }
      handleClose();
    },
    [onOptionClick, handleClose],
  );*/

  const outsideClickRef = useOutsideClick<HTMLDivElement>(() => {
    handleClose();
  });

  return (
    <div className={b({opened: toggled})} ref={outsideClickRef}>
      <div className={b('wrapper')} onClick={handleToggle}>
        <div className={b('btn')} onClick={handleClick}>
          {icon}
          <span className={b('btn-text')}>{label}</span>
        </div>
        <div className={b('btn-icon')}>
          <ChevronIcon />
        </div>
      </div>
      {toggled && <div className={b('pop-up')}>{children}</div>}
    </div>
  );
}

Dropdown.defaultProps = {
  label: '',
};
