import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';

import {useUser} from '../../services/user';
import {BalanceTableItem} from './balance-table-item';

const b = bem('transaction-table');

export const BalanceTable = () => {
  const {user} = useUser();

  const balances = user?.balance || [];
  const balancesToArray = Object.entries(balances);

  return (
    <div className={b()}>
      <Tables.Table>
        <Tables.Head>
          <Tables.Row>
            <Tables.HeadCell>Coin</Tables.HeadCell>
            <Tables.HeadCell>Available</Tables.HeadCell>
            <Tables.HeadCell>On Orders</Tables.HeadCell>
          </Tables.Row>
        </Tables.Head>
        <Tables.Body>
          {balancesToArray.map((item, index) => (
            // @ts-ignore
            <BalanceTableItem key={index} coin={item[0]} value={item[1]} />
          ))}
        </Tables.Body>
      </Tables.Table>
    </div>
  );
};
