import React from 'react';
import dayjs from 'dayjs';

import {Row, Cell} from 'shared/ui/tables';
import {DepositData} from 'services/user/types';

type Props = {
  deposit: DepositData;
};

export function TransactionTableItem({deposit}: Props) {
  const {amount, coin, date, network, status, address} = deposit;

  return (
    <Row>
      <Cell>{coin}</Cell>
      <Cell>{dayjs(date).format('HH:mm:ss DD.MM.YYYY')}</Cell>
      <Cell>{amount}</Cell>
      <Cell>{network}</Cell>
      <Cell>{address}</Cell>
      <Cell>{status}</Cell>
    </Row>
  );
}
