import React, {useEffect} from 'react';
import cn from 'classnames';

import {ReactComponent as CrossIcon} from 'assets/cross.svg';

import {Portal} from '../portal';
import {Backdrop} from '../backdrop';
import {b} from './consts';

import './modal.scss';

const ESCAPE_KEY = 27;

interface Props extends React.HTMLProps<HTMLDivElement> {
  onClose?: () => void;
  side?: boolean;
  classNameContent?: string;
}

function CloseButton(props: Omit<React.HTMLProps<HTMLButtonElement>, 'type'>) {
  return (
    <button className={b('close-btn')} {...props}>
      <CrossIcon />
    </button>
  );
}

type ContentProps = React.HTMLProps<HTMLDivElement> & {className?: string};

function Content({className, ...props}: ContentProps) {
  const cls = cn(b('content'), className);
  return <div className={cls} {...props} />;
}

export function Modal({className, classNameContent, children, onClose, side, ...props}: Props) {
  const cls = cn(b(), className);

  const handleClose = () => {
    if (onClose) {
      onClose();
    }
  };

  useEffect(() => {
    const handleKeyDown = ({keyCode}: KeyboardEvent) => {
      if (keyCode === ESCAPE_KEY) {
        handleClose();
      }
    };

    window.document.addEventListener('keydown', handleKeyDown);
    return () => {
      window.document.removeEventListener('keydown', handleKeyDown);
    };
  });

  return (
    <Portal root="modals">
      <Backdrop>
        <div className={cls} {...props}>
          <Content className={classNameContent}>
            {onClose && !side && <CloseButton onClick={handleClose} />}
            {children}
          </Content>
        </div>
      </Backdrop>
    </Portal>
  );
}
