import {useState} from 'react';

import api from 'services/http';

import {ACCESS_TOKEN, REFRESH_TOKEN} from './consts';
import {AuthResponse} from './types';

export type loginHandler = (login: string, password: string) => Promise<void>;

export type signUpHandler = (login: string, password: string, referral: string | null) => Promise<void>;

export type logOutHandler = () => Promise<void>;

export type AuthService = {
  isLoggedIn: boolean;
  login: loginHandler;
  signUp: signUpHandler;
  logOut: logOutHandler;
};

export const useAuthService = (): AuthService => {
  const [isLoggedIn, setIsLoggedIn] = useState(Boolean(localStorage.getItem(ACCESS_TOKEN)));

  const login = async (login: string, password: string) => {
    const body = {
      username: login,
      password: password,
    };

    const data: AuthResponse = await api.post('/login', body);

    if (data && data.success) {
      localStorage.setItem(ACCESS_TOKEN, data.token);
      localStorage.setItem(REFRESH_TOKEN, data.refreshToken);
      setIsLoggedIn(true);
    }
  };

  const signUp = async (login: string, password: string, referral: string | null) => {
    const body = {
      username: login,
      password,
      referral,
    };

    const data: AuthResponse = await api.post('/registration', body);
    if (data && data.success) {
      // localStorage.setItem(ACCESS_TOKEN, data.access);
      // localStorage.setItem(REFRESH_TOKEN, data.refresh);
      setIsLoggedIn(true);
    }
  };

  const logOut: logOutHandler = async () => {
    localStorage.removeItem(ACCESS_TOKEN);
    localStorage.removeItem(REFRESH_TOKEN);
    setIsLoggedIn(false);
  };

  return {
    isLoggedIn,
    login,
    signUp,
    logOut,
  };
};
