import {useState} from 'react';

import api from 'services/http';
import {User, VerificationStatus} from 'services/user/types';

import {Chat, Deposit, ResponseChats, ResponseDeposit, ResponseUsers, ResponseWithdraws} from './types';

export type getDepositHandler = () => Promise<void>;
export type getUsersHandler = () => Promise<void>;
export type getChatsHandler = () => Promise<void>;
export type updateVerificationStatusHandler = (userId: string, status: VerificationStatus) => Promise<void>;

// TODO rename deposit & withdraw
export type updateDepositHandler = (
  id: string,
  status: 'Failed' | 'Success',
  userId: string,
  amount: string,
  coin: string,
) => Promise<void>;

export type UseService = {
  deposits: Deposit[];
  getDeposits: getDepositHandler;
  updateDepositStatus: updateDepositHandler;

  withdraws: Deposit[];
  getWithdraws: getDepositHandler;
  updateWithdrawStatus: updateDepositHandler;

  users: User[];
  getUsers: getUsersHandler;

  chats: Chat[];
  getChats: getChatsHandler;
  updateVerificationStatus: updateVerificationStatusHandler;
};

export const useAdminService = (): UseService => {
  const [deposits, setDeposits] = useState<Deposit[]>([]);
  const [withdraws, setWithdraws] = useState<Deposit[]>([]);
  const [users, setUsers] = useState<User[]>([]);
  const [chats, setChats] = useState<Chat[]>([]);

  const getChats = async () => {
    const data: ResponseChats = await api.get('/chat-list');
    setChats(data.chats);
  };

  const getDeposits = async () => {
    const data: ResponseDeposit = await api.get('/deposit-list');
    setDeposits(data.deposits);
  };

  const getWithdraws = async () => {
    const data: ResponseWithdraws = await api.get('/withdraw-list');
    setWithdraws(data.withdraws);
  };

  const getUsers = async () => {
    const data: ResponseUsers = await api.get('/users');
    setUsers(data.users);
  };

  const updateDepositStatus = async (
    id: string,
    status: 'Failed' | 'Success',
    userId: string,
    amount: string,
    coin: string,
  ) => {
    await api.post('/update-deposit', {id, status, userId, amount, coin});
  };

  const updateWithdrawStatus = async (
    id: string,
    status: 'Failed' | 'Success',
    userId: string,
    amount: string,
    coin: string,
  ) => {
    await api.post('/update-withdraw', {id, status, userId, amount, coin});
  };

  const updateVerificationStatus = async (userId: string, status: VerificationStatus) => {
    await api.post('/update-verification', {userId, status});
  };

  return {
    deposits,
    getDeposits,
    updateDepositStatus,
    withdraws,
    getWithdraws,
    updateWithdrawStatus,
    users,
    getUsers,
    chats,
    getChats,
    updateVerificationStatus,
  };
};
