import React, {useState, useEffect, useMemo, useRef} from 'react';
import cn from 'classnames';

import {Button} from 'shared/ui/button';
import {bem} from 'shared/bem';

import './file-input.scss';
import {CheckIcon, WarnIcon} from '../icons';

const b = bem('file-input');

type ElementProps = React.HTMLProps<HTMLInputElement>;

type OwnProps = {
  insertLabel?: string;
  clearLabel?: string;
  value: FileList | null;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
};

type Props = Omit<ElementProps, 'onChange' | 'type' | 'value'> & OwnProps;

export function FileInput({className, value, insertLabel, clearLabel, onChange, ...props}: Props) {
  const [files, setFiles] = useState<FileList | undefined | null>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    setFiles(value);
  }, [value]);

  const text = useMemo(() => {
    if (!files) {
      return;
    }

    return Array.from(files)
      .map((x) => x.name)
      .join(', ');
  }, [files]);

  return (
    <div className={cn(b(), className)}>
      <label className={b('label')}>
        <input ref={inputRef} type="file" onChange={onChange} {...props} />
        <Button className={b('btn', {active: Boolean(text)})} kind="primary">
          {Boolean(text) ? <CheckIcon /> : <WarnIcon />}
          {Boolean(text) ? 'Documents have been uploaded successfully' : insertLabel}
        </Button>
      </label>
    </div>
  );
}

FileInput.defaultProps = {
  insertLabel: 'Click here to upload photo of your passport id',
};
