import React, {useEffect, useState} from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';
import {useUser} from 'services/user';
import {Roles} from 'shared/utils';

import {Item} from './item';
import {navPersonItems} from './consts';
import {HeaderItem} from '../navbar/types';
import './profile-sidebar.scss';

const b = bem('profile-sidebar');

type Props = {className?: string};

export const ProfileSidebar = ({className}: Props) => {
  const {user} = useUser();
  const [nav, setNav] = useState<HeaderItem[]>([]);
  const cls = cn(b(), className);

  useEffect(() => {
    const filteredByRoleNav = navPersonItems.filter((item) => item.permittedRoles.includes(user?.role || Roles.ANONIM));
    setNav(filteredByRoleNav);
  }, [user]);

  return (
    <div className={cls}>
      {nav.map((item) => (
        <Item key={item.link} item={item} />
      ))}
    </div>
  );
};
