import React, {useEffect} from 'react';
import {useSearchParams} from 'react-router-dom';

import {bem} from 'shared/bem';
import {useAdminService} from 'services/admin';
import {User} from 'services/user/types';
import {Button} from 'shared/ui';

import {UserProfileBalanceTable} from './user-profile-balance-table';
import {UserProfileReferralTable} from './user-profile-referral-table';

import './user-profile.scss';

const b = bem('user-profile');

export const UserProfile = () => {
  const {users, getUsers, updateVerificationStatus} = useAdminService();
  const [searchParams] = useSearchParams();
  const userId = searchParams.get('id');
  const user: User | undefined = users.find((item) => item.id === userId);

  useEffect(() => {
    getUsers();
  }, []);

  const balancesToArray = Object.entries(user?.balance || []);

  const handleUpdateToSuccess = () => {
    if (user) updateVerificationStatus(user?.id, 'SUCCESS');
  };

  const handleUpdateToFailed = () => {
    if (user) updateVerificationStatus(user?.id, 'FAILED');
  };

  return (
    <div className={b()}>
      <div>
        <span className={b('text-username')}>{user?.username}</span>
        <span className={b('text-id')}>{user?.id}</span>
      </div>
      <UserProfileBalanceTable balances={balancesToArray} />
      {user?.docs && (
        <div className={b('docs-container')}>
          <div>National: {user.docs.national}</div>
          <div>First name: {user.docs.firstName}</div>
          <div>Last name: {user.docs.lastName}</div>
          <div>Passport ID: {user.docs.passportId}</div>
          <div>Passport ID: {user.docs.passportId}</div>
          <div className={b('wrapper')}>
            <div>
              Current status verified:
              <span className={b('mark-status')}>{user.verified ? 'verified' : 'no verified'}</span>
            </div>
            <div>
              Current status operation:
              <span className={b('mark-status')}>{user.verificationStatus}</span>
            </div>
            <Button onClick={handleUpdateToSuccess} disabled={user.verified} className={b('btn-submit')}>
              Success
            </Button>
            <Button onClick={handleUpdateToFailed} className={b('btn-submit')}>
              Failed
            </Button>
          </div>
          <div className={b('image-container')}>
            <img className={b()} src={user.docs.faceImage} />
            <img src={user.docs.passportImage} />
          </div>
        </div>
      )}
      <UserProfileReferralTable referrals={user?.referrals || []} />
    </div>
  );
};
