import React, {useEffect} from 'react';

import {bem} from 'shared/bem';
import {useAdminService} from 'services/admin';
import * as Tables from 'shared/ui/tables';
import {TableContentBlank} from 'components/table-content-blank';

import {UserItem} from './user-item';
import './users.scss';

const b = bem('users');

export const Users = () => {
  const {users, getUsers} = useAdminService();

  useEffect(() => {
    getUsers();
  }, []);

  return (
    <div className={b()}>
      <Tables.Table>
        <Tables.Caption>Users</Tables.Caption>
        {users.length ? (
          <>
            <Tables.Head>
              <Tables.Row>
                <Tables.HeadCell>User ID</Tables.HeadCell>
                <Tables.HeadCell>Username</Tables.HeadCell>
                <Tables.HeadCell>Role</Tables.HeadCell>
                <Tables.HeadCell>Verified</Tables.HeadCell>
                <Tables.HeadCell>Referrals</Tables.HeadCell>
              </Tables.Row>
            </Tables.Head>
            <Tables.Body>
              {users.map((item) => (
                <UserItem key={item.id} user={item} />
              ))}
            </Tables.Body>
          </>
        ) : (
          <Tables.Body>
            <Tables.Row>
              <Tables.Cell>
                <TableContentBlank message={'No users'} />
              </Tables.Cell>
            </Tables.Row>
          </Tables.Body>
        )}
      </Tables.Table>
    </div>
  );
};
