import React, {useCallback, useState} from 'react';

import {Button, Form, FormGroup, Input, Label, WarnIcon} from 'shared/ui';
import {FileInput} from 'shared/ui/file-input';
import {bem} from 'shared/bem';

import './verification.scss';

const b = bem('verification');

export const Verification = () => {
  const [national, setNational] = useState('');
  const [passportId, setPassportId] = useState('');
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');

  const handleChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    switch (e.target.id) {
      case 'nationally':
        setNational(e.target.value);
        break;
      case 'passport':
        setPassportId(e.target.value);
        break;
      case 'firstName':
        setFirstName(e.target.value);
        break;
      case 'lastName':
        setLastName(e.target.value);
        break;
    }
  }, []);

  const handleSubmit = () => {
    console.log('submit');
  };

  const [files, setFiles] = useState<FileList | null>(null);
  const [filesFace, setFilesFace] = useState<FileList | null>(null);

  const handleChangeFiles = (e: React.ChangeEvent<HTMLInputElement>) => {
    const fileList = e.target.files;

    setFiles(fileList);
  };

  const handleChangeFilesFace = (e: React.ChangeEvent<HTMLInputElement>) => {
    const fileList = e.target.files;

    setFilesFace(fileList);
  };

  return (
    <div className={b()}>
      <div className={b('header')}>ID Verification</div>
      <p className={b('sub-header')}>
        <span className={b('sub-header-text')}>
          <WarnIcon />
          The information you fill in must be consistent with the information in your ID documents.
        </span>
      </p>
      <Form className={b('form')} onSubmit={handleSubmit}>
        <FormGroup className={b('form-group')}>
          <Label htmlFor={'nationally'} className={b('label')}>
            Nationally
          </Label>
          <Input className={b('input')} id={'nationally'} value={national} onChange={handleChange} />
        </FormGroup>

        <FormGroup className={b('form-group')}>
          <Label htmlFor={'passport'} className={b('label')}>
            Passport
          </Label>
          <Input className={b('input')} id={'passport'} value={passportId} onChange={handleChange} />
        </FormGroup>

        <FormGroup className={b('form-group')}>
          <Label htmlFor={'firstName'} className={b('label')}>
            First name
          </Label>
          <Input className={b('input')} id={'firstName'} value={firstName} onChange={handleChange} />
        </FormGroup>

        <FormGroup className={b('form-group')}>
          <Label htmlFor={'lastName'} className={b('label')}>
            Last name
          </Label>
          <Input className={b('input')} id={'lastName'} value={lastName} onChange={handleChange} />
        </FormGroup>

        <FormGroup className={b('form-group')}>
          <Label htmlFor={'document-file'} className={b('label')}>
            Photo of ID
          </Label>
          <FileInput id={'document-file'} value={files} onChange={handleChangeFiles} />
        </FormGroup>

        <FormGroup className={b('form-group')}>
          <Label htmlFor={'document-face-file'} className={b('label')}>
            Photo where you keep your documents and paper with today&apos;s date and the domain of the Exchange
          </Label>
          <FileInput
            id={'document-face-file'}
            value={filesFace}
            onChange={handleChangeFilesFace}
            insertLabel={'Click here to upload photo with your documents and face'}
          />
        </FormGroup>

        <FormGroup className={b('form-group')}>
          <Label htmlFor={'document-face-file'} className={b('label')}>
            Support JPG or PNG format. The document must be smaller than 5M; Information in the ID or passport,
            especially the ID or passport No. and the name, shall be clear and visible without any modification or
            cover.
          </Label>
        </FormGroup>

        <FormGroup className={b('form-group')}>
          <Button onClick={handleSubmit} type={'submit'} kind={'primary'} className={b('btn-submit')}>
            Withdraw
          </Button>
        </FormGroup>
      </Form>
    </div>
  );
};
