import React, {useCallback, useEffect, useState} from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';
import {SymbolInfo} from 'services/binance/use-binance-service';
import {useUser} from 'services/user';
import {Form, FormGroup, Input, Label} from 'shared/ui';

import './elevation.scss';

const b = bem('elevation');

type Props = {
  className?: string;
  type: 'SELL' | 'BUY';
  symbolInfo: SymbolInfo;
  price: string;
};

export const ElevationTrade = ({className, type, symbolInfo, price}: Props) => {
  const {user, getUser, updateBalance} = useUser();
  const cls = cn(b(), className);
  const [value, setValue] = useState('');

  const newPriceForXRPUSDT = symbolInfo.symbol === 'XRPUSDT' ? (Number(price) * 1.05).toFixed(6) : price;

  useEffect(() => {
    getUser();
  }, []);

  const handleChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setValue(e.target.value);
  }, []);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (type === 'SELL') {
      const quoteValue = Number(newPriceForXRPUSDT) * Number(value);
      updateBalance({
        quoteAsset: symbolInfo.quoteAsset,
        quoteValue: quoteValue.toString(),
        baseAsset: symbolInfo.baseAsset,
        baseValue: '-' + value,
      }).then(() => {
        getUser();
      });
    } else {
      const quoteValue = Number(newPriceForXRPUSDT) * Number(value);
      updateBalance({
        quoteAsset: symbolInfo.quoteAsset,
        quoteValue: '-' + quoteValue.toString(),
        baseAsset: symbolInfo.baseAsset,
        baseValue: value,
      }).then(() => {
        getUser();
      });
    }
  };

  const total = Number(newPriceForXRPUSDT) * Number(value);

  const asset: string = type === 'BUY' ? symbolInfo.quoteAsset : symbolInfo.baseAsset;

  const disabledBtn = () => {
    if (value === '' || total < 0) return true;
    if (type === 'SELL') {
      if (user?.balance[symbolInfo.quoteAsset]) {
        return Number(user?.balance[symbolInfo.baseAsset]) < Number(value);
      } else return true;
    } else {
      if (user?.balance[symbolInfo.quoteAsset]) {
        return total > Number(user?.balance[symbolInfo.quoteAsset]);
      } else return true;
    }

    return true;
  };

  return (
    <div className={cls}>
      <Form className={b('form')} onSubmit={handleSubmit}>
        <p className={b('form-header')}>
          <span className={b('form-header-label')}>Balance:</span>
          <span className={b('form-header-value')}>
            <span className={b('balance-value')}>{user?.balance[asset]}</span>
            <span>{asset}</span>
          </span>
        </p>
        <FormGroup>
          <Label htmlFor={'username'} className={b('label')}>
            Price
          </Label>
          <Input
            disabled={true}
            className={b('input')}
            id={'username'}
            value={newPriceForXRPUSDT}
            onChange={handleChange}
          />
        </FormGroup>
        <FormGroup>
          <Label htmlFor={'username'} className={b('label')}>
            Amount
          </Label>
          <Input className={b('input')} id={'username'} value={value} onChange={handleChange} />
        </FormGroup>
        <div>Total: {total}</div>
        <button disabled={disabledBtn()} type={'submit'} className={b('btn')}>
          {type}
        </button>
      </Form>
    </div>
  );
};
