import React, {useEffect} from 'react';
import {useLocation} from 'react-router-dom';

import {useUser} from 'services/user';
import {bem} from 'shared/bem';
import {Roles} from 'shared/utils';
import {Dashboard, Deposit, Settings, ProfileSidebar, Support, Verification, Withdraw} from 'components/personal';
import {
  Deposit as DepositAdmin,
  Settings as SettingsAdmin,
  Support as SupportAdmin,
  Users as UsersAdmin,
  Withdraw as WithdrawAdmin,
  UserProfile,
} from 'components/admin';
import {AdminSupportChat} from 'components/admin/support/admin-support-chat';

import './profile.scss';

const b = bem('profile');

export const Profile = () => {
  const {pathname} = useLocation();
  const {getUser, getDictionary, user} = useUser();

  useEffect(() => {
    getUser();
    getDictionary();
  }, []);

  const getScreen = () => {
    if (user?.role) {
      switch (pathname) {
        case '/profile':
          return user?.role === Roles.ADMIN ? null : <Dashboard />;
        case '/deposit':
          return user?.role === Roles.ADMIN ? <DepositAdmin /> : <Deposit />;
        case '/withdraw':
          return user?.role === Roles.ADMIN ? <WithdrawAdmin /> : <Withdraw />;
        case '/settings':
          return user?.role === Roles.ADMIN ? <SettingsAdmin /> : <Settings />;
        case '/support':
          return user?.role === Roles.ADMIN ? <SupportAdmin /> : <Support />;
        case '/verification':
          return user?.role === Roles.ADMIN ? <WithdrawAdmin /> : <Verification />;
        case '/user-profile':
          return user?.role === Roles.ADMIN ? <UserProfile /> : null;
        case '/users':
          return user?.role === Roles.ADMIN ? <UsersAdmin /> : null;
        case '/support/chat':
          return user?.role === Roles.ADMIN ? <AdminSupportChat /> : null;
        default:
          return <div>404</div>;
      }
    }

    return <div>..loading</div>;
  };

  return (
    <div className={b()}>
      <div className={b('container')}>
        <ProfileSidebar />
        <div className={b('wrapper')}>{getScreen()}</div>
      </div>
    </div>
  );
};
