import React, {useCallback, useState} from 'react';

import {Input} from 'shared/ui';
import {Options} from 'shared/types';
import {bem} from 'shared/bem';
import {getIcon} from 'shared/utils';

import {SelectModal} from '../index';
import {SelectModalItem} from '../select-modal-item';

import './select-options.scss';

const b = bem('select-options');

const optionsCurrency: Options = [
  {label: 'BTC', value: 'BTC'},
  {label: 'USDT', value: 'USDT'},
];

type Props = {
  onClose: () => void;
  onSelect: (value: string) => void;
  label: string;
  options: Options;
};

export const SelectOptions = ({onClose, label, options, onSelect}: Props) => {
  const [valueCurrency, setValueCurrency] = useState('');

  const handleChangeCurrency = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setValueCurrency(e.target.value);
  }, []);

  const filterOptions = options.filter((item) => item.value.toLowerCase().match(valueCurrency.toLocaleLowerCase()));

  return (
    <SelectModal label={label} options={optionsCurrency} onClose={onClose}>
      <>
        <Input value={valueCurrency} onChange={handleChangeCurrency} />
        <div className={b('list')}>
          {filterOptions.map((item) => (
            <SelectModalItem
              key={item.label}
              label={item.label}
              value={item.value}
              onSelect={onSelect}
              onClose={onClose}
              icon={item.link ? null : getIcon(item.value)}
            />
          ))}
        </div>
      </>
    </SelectModal>
  );
};
