import React, {useCallback, useState} from 'react';

import {Button, Form, FormGroup, Input, Label, WarnIcon} from 'shared/ui';
import {bem} from 'shared/bem';
import {useUser} from 'services/user';
import {DepositData, Network} from 'services/user/types';
import {SelectOptions} from 'components/select-modal/select-options';
import {TransactionTable} from 'components/transaction-table';

import './deposit.scss';
import {Modal, ModalBody, ModalHeader} from '../../../shared/ui/modal';
import {ReactComponent as CrossIcon} from '../../../assets/cross.svg';

const b = bem('deposit');

export const Deposit = () => {
  const {
    user,
    sendDeposit,
    dictionary: {networks, coins},
  } = useUser();

  const [coin, setCurrency] = useState('BTC');
  const [isOpenModal, setOpenModal] = useState<boolean>(false);
  const [isOpenPaymentModal, setOpenPaymentModal] = useState<boolean>(false);

  const [network, setNetwork] = useState('');
  const [isOpenModalNet, setOpenModalNet] = useState<boolean>(false);
  const [address, setAddress] = useState<string>('');

  const [amount, setAmount] = useState('');

  const handleChangeAmount = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setAmount(e.target.value);
  }, []);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const depositData: DepositData = {
      network,
      coin,
      amount,
    };

    sendDeposit(depositData).then((data) => {
      if (data) {
        setAddress(data);
        setOpenPaymentModal(true);
      }
    });
  };

  const canSubmit = Boolean(coin.trim()) && Boolean(network.trim()) && Boolean(amount.trim());

  const filtersSuccessNetwork = networks.filter((item: Network) => item.link?.includes(coin));

  return (
    <>
      <div className={b()}>
        <h5 className={b('header')}>Deposit</h5>
        <Form className={b('form')} onSubmit={handleSubmit}>
          <FormGroup className={b('form-group')}>
            <Label htmlFor={'currency'} className={b('label')}>
              Currency
            </Label>
            <Input
              className={b('input')}
              onClick={() => setOpenModal(true)}
              readOnly={true}
              id={'currency'}
              value={coin}
              placeholder={'Select currency'}
            />
          </FormGroup>
          <FormGroup className={b('form-group')}>
            <Label htmlFor={'network'} className={b('label')}>
              Network
            </Label>
            <Input
              className={b('input')}
              onClick={() => setOpenModalNet(true)}
              readOnly={true}
              id={'currency'}
              value={network}
              placeholder={'Select Network'}
            />
          </FormGroup>
          <div className={b('warn')}>
            <div className={b('warn-logo')}>
              <WarnIcon />
            </div>
            <div className={b('warn-text')}>
              Ensure the network you choose to deposit matches the withdrawal network, or assets may be lost.
            </div>
          </div>
          <FormGroup className={b('form-group')}>
            <Label htmlFor={'amount'} className={b('label')}>
              Amount
            </Label>
            <Input
              className={b('input')}
              id={'amount'}
              value={amount}
              onChange={handleChangeAmount}
              placeholder={'0.00'}
              type={'number'}
            />
          </FormGroup>
          <FormGroup className={b('form-group')}>
            <Button
              onClick={handleSubmit}
              type={'submit'}
              kind={'primary'}
              disabled={!canSubmit}
              className={b('btn-submit')}
            >
              Deposit
            </Button>
          </FormGroup>
        </Form>
      </div>

      <TransactionTable operations={user?.deposits || []} tableName={'Deposit requests'} />

      {isOpenModal && (
        <SelectOptions label={'Currency'} options={coins} onClose={() => setOpenModal(false)} onSelect={setCurrency} />
      )}
      {isOpenModalNet && (
        <SelectOptions
          label={'Network'}
          options={filtersSuccessNetwork}
          onClose={() => setOpenModalNet(false)}
          onSelect={setNetwork}
        />
      )}
      {isOpenPaymentModal && (
        <Modal className={b('modal')}>
          <ModalHeader className={b('header-modal')}>
            <div>Payment</div>
            <CrossIcon onClick={() => setOpenPaymentModal(false)} className={b('cross-icon')} />
          </ModalHeader>
          <ModalBody>
            <div className={b('modal-address')}>{address}</div>
            <div>
              Close this window after you complete the payment. The deposit request will be closed after 3 hours.
            </div>
          </ModalBody>
        </Modal>
      )}
    </>
  );
};
