import React from 'react';

import {Modal, ModalBody, ModalHeader} from 'shared/ui/modal';
import {bem} from 'shared/bem';
import {Options} from 'shared/types';
import {ReactComponent as CrossIcon} from 'assets/cross.svg';

import './select-modal.scss';

const b = bem('select-modal');

type Props = {
  onClose: () => void;
  options: Options;
  label: string;
  children: React.ReactNode;
};

export const SelectModal = ({onClose, label, children}: Props) => {
  return (
    <Modal className={b()} classNameContent={b('content')} onClose={onClose} side={true}>
      <ModalHeader className={b('header')}>
        <div>{label}</div>
        <CrossIcon onClick={onClose} className={b('cross-icon')} />
      </ModalHeader>
      <ModalBody>{children}</ModalBody>
    </Modal>
  );
};
