import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';

import './elevatoin-table.scss';

type Props = React.HTMLProps<HTMLTableRowElement> & {
  className?: string;
};

const b = bem('elevation-table');

export const ElevationTableRow = ({className, ...rest}: Props) => {
  const cls = cn(b('row'), className);
  return <tr className={cls} {...rest} />;
};
