import React, {useEffect} from 'react';

import {bem} from 'shared/bem';
import {TransactionAdminTable} from 'components/transaction-admin-table';
import {useAdminService} from 'services/admin';

import './withdraw.scss';

const b = bem('withdraw');

export const Withdraw = () => {
  const {withdraws, updateWithdrawStatus, getWithdraws} = useAdminService();

  const handleUpdateDepositStatus = (
    id: string,
    status: 'Success' | 'Failed',
    userId: string,
    amount: string,
    coin: string,
  ) => {
    updateWithdrawStatus(id, status, userId, amount, coin).then(() => {
      getWithdraws();
    });
  };

  useEffect(() => {
    getWithdraws();
  }, []);

  return (
    <div className={b()}>
      <h5 className={b('header')}>Deposits</h5>
      <TransactionAdminTable operations={withdraws} updateOperation={handleUpdateDepositStatus} />
    </div>
  );
};
