import React, {useEffect} from 'react';

import {bem} from 'shared/bem';
import {TransactionAdminTable} from 'components/transaction-admin-table';
import {useAdminService} from 'services/admin';

import './deposit.scss';

const b = bem('deposit');

export const Deposit = () => {
  const {updateDepositStatus, deposits, getDeposits} = useAdminService();

  const handleUpdateDepositStatus = (
    id: string,
    status: 'Success' | 'Failed',
    userId: string,
    amount: string,
    coin: string,
  ) => {
    updateDepositStatus(id, status, userId, amount, coin).then(() => {
      getDeposits();
    });
  };

  useEffect(() => {
    getDeposits();
  }, []);

  return (
    <div className={b()}>
      <h5 className={b('header')}>Deposits</h5>
      <TransactionAdminTable operations={deposits} updateOperation={handleUpdateDepositStatus} />
    </div>
  );
};
