import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';
import {TableContentBlank} from 'components/table-content-blank';

import {UserProfileReferralItem} from './user-profile-referral-item';
import './user-profile.scss';

const b = bem('user-profile');

type Props = {
  referrals: string[];
};

export const UserProfileReferralTable = ({referrals}: Props) => {
  return (
    <Tables.Table className={b('table')}>
      <Tables.Caption className={b('caption')}>Referrals</Tables.Caption>
      {referrals.length ? (
        <>
          <Tables.Head>
            <Tables.Row>
              <Tables.HeadCell className={b('head-cell')}>Username</Tables.HeadCell>
            </Tables.Row>
          </Tables.Head>
          <Tables.Body>
            {referrals.map((item) => (
              <UserProfileReferralItem key={item} referral={item} />
            ))}
          </Tables.Body>
        </>
      ) : (
        <Tables.Body>
          <Tables.Row>
            <Tables.Cell>
              <TableContentBlank message={'No balances'} />
            </Tables.Cell>
          </Tables.Row>
        </Tables.Body>
      )}
    </Tables.Table>
  );
};
