import React from 'react';
import cn from 'classnames';

import {b} from './consts';

import './table.scss';

type ElementProps = React.HTMLProps<HTMLTableElement>;

type OwnProps = {
  fixed?: boolean;
  className?: string;
};

type Props = ElementProps & OwnProps;

export function Table({fixed, className, ...rest}: Props) {
  const cls = cn(b({fixed}), className);
  return <table className={cls} {...rest} />;
}
