import React, {useState} from 'react';

import {useAuth} from 'services/auth';
import {WalletIcon, UserIcon, HamburgerIcon} from 'shared/ui';
import {Dropdown} from 'shared/ui/dropdown';
import {bem} from 'shared/bem';
import {NavbarModal} from 'components/navbar-modal';

import {ExtendArea} from '../extend-area';
import {ExtendAssets} from '../extend-assets';
import {AuthButtonsGroup} from '../../auth-buttons-group';
import './navbar-user.scss';

const b = bem('navbar-user');

export const NavbarUser = () => {
  const {isLoggedIn} = useAuth();
  const [isShowModal, setShowModal] = useState<boolean>(false);

  const handleCloseModal = () => {
    setShowModal(false);
  };

  const handleOpenModal = () => {
    setShowModal(true);
  };

  return (
    <div className={b()}>
      <div className={b('nav-group')}>
        {isLoggedIn ? (
          <div className={b('dropdown-group')}>
            <Dropdown icon={<WalletIcon />} label={'Assets'}>
              <ExtendAssets />
            </Dropdown>
            <Dropdown icon={<UserIcon />} label={'Personal Area'}>
              <ExtendArea />
            </Dropdown>
          </div>
        ) : (
          <AuthButtonsGroup />
        )}
        <div className={b({authenticated: isLoggedIn})}>Help Center</div>
      </div>
      <div onClick={handleOpenModal} className={b('hamburger', {authenticated: isLoggedIn})}>
        <HamburgerIcon />
      </div>
      {isShowModal && <NavbarModal onClose={handleCloseModal} />}
    </div>
  );
};
