const b = bem('users');

import './dashboard.scss';

export const Dashboard = () => {
  const {user} = useUser();

  return (
    <div className={b()}>
      <div className={b('overview')}>
        <UserIcon />
        <div className={b('user-info')}>
          <p className={b('text-main')}>
            {user?.username}
            <span className={b('text-main_sub')}>(UID:{user?.id})</span>
          </p>
          <p className={b('text-secondary')}>
            <span>
              Referral link: {location.host}/signup?ref={user?.username}
            </span>
          </p>
        </div>
      </div>
      <BalanceTable />
    </div>
  );
};
