import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';

import './label.scss';

type ElementProps = React.HTMLProps<HTMLLabelElement>;

type OwnProps = {
  required?: boolean;
  className?: string;
};

type Props = ElementProps & OwnProps;

const b = bem('label');

export function Label({required, className, ...props}: Props) {
  const cls = cn(b({required}), className);
  return <label className={cls} {...props} />;
}
