import axios, {AxiosRequestConfig, AxiosResponse} from 'axios';

// const prodMasterURL = 'https://adavasta.herokuapp.com/api';
// const prodDevURL = 'https://vasta.herokuapp.com/api';
// const prodDev1URL = 'https://hostdevsv.herokuapp.com/api';
const master = 'https://adavasta.com/api';
// const devURL = 'http://localhost:5000/api';

const DEFAULT_CONFIG: AxiosRequestConfig = {
  baseURL: master,
  withCredentials: true,
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json',
  },
};

axios.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');

  if (token && token !== 'undefined') {
    if (config?.headers) {
      config.headers.Authorization = `Bearer ${token}`;
    }
  }

  if (config.url === '/logout') {
    localStorage.removeItem('token');
  }

  return config;
});

axios.interceptors.response.use(
  function (response) {
    const token = response.data.token;
    if (token) {
      localStorage.setItem('token', token);
    }
    return response;
  },
  function (error) {
    if (error.response.status === 401) {
      return axios.get('/refresh', DEFAULT_CONFIG).then(({data}) => {
        localStorage.setItem('token', data.token);
        return axios.request(error.config);
      });
    }

    return Promise.reject(error);
  },
);

function get<Req, Res>(url: string): Promise<Res> {
  const config: AxiosRequestConfig = {
    method: 'GET',
    ...DEFAULT_CONFIG,
  };

  return axios.get<Req, AxiosResponse<Res>>(url, config).then(({data}) => data);
}

function post<Req, Res>(url: string, body?: Req): Promise<Res> {
  return axios.post<Req, AxiosResponse<Res>>(url, body, DEFAULT_CONFIG).then(({data}) => data);
}

const http = {
  get,
  post,
};

export default http;
