import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';

import './elevatoin-table.scss';

type Props = React.HTMLProps<HTMLTableCellElement> & {
  className?: string;
  valid?: boolean;
};

const b = bem('elevation-table');

export const ElevationTableCell = ({className, valid, ...rest}: Props) => {
  const cls = cn(b('cell', {green: valid === true, red: valid === false}), className);
  return <td className={cls} {...rest} />;
};
