import React from 'react';
import dayjs from 'dayjs';

import {bem} from 'shared/bem';
import {Message} from 'services/chat/types';

import './admin-message-item.scss';

const b = bem('admin-message-item');

type Props = {
  message: Message;
  userId?: string;
};

export const AdminMessageItem = ({message, userId}: Props) => {
  const {message: msg, date} = message;

  return (
    <div className={b({admin: message.admin})}>
      <div className={b('header')}>
        <span className={b('header-username')}>
          {message.admin ? null : 'UID:'}
          <span className={b('header-username-light')}>{message.admin ? 'Admin' : userId}</span>
        </span>
        <span className={b('header-date')}>{dayjs(date).format('HH:mm:ss DD.MM.YYYY')}</span>
      </div>
      <div className={b('body')}>{msg}</div>
    </div>
  );
};
