import {useEffect, useState} from 'react';

import api from 'services/http';

import {Depth, ResponseDepth, ResponseTicker, ResponseTrade, Ticker, Trade} from './types';

const DEPTH_DEFAULT: Depth = {
  lastUpdateId: 0,
  bids: [],
  asks: [],
};

type getTickerHandler = () => Promise<void>;
type getDepthHandler = () => Promise<void>;
type getTradesHandler = () => Promise<void>;
type getSymbolInfoHandler = () => Promise<void>;
type onChangeCurrentCoin = (symbol: string) => void;

type UseBinanceService = {
  tickers: Ticker[];
  getTickers: getTickerHandler;
  depth: Depth;
  getDepth: getDepthHandler;
  trades: Trade[];
  getTrades: getTradesHandler;
  activeCoin: string;
  onChangeActiveCoin: onChangeCurrentCoin;
  symbolInfo: SymbolInfo;
  getSymbolInfo: getSymbolInfoHandler;
};

export type SymbolInfo = {
  symbol: string;
  baseAsset: string;
  quoteAsset: string;
};

export const SYMBOL_INFO_DEFAULT: SymbolInfo = {
  symbol: 'BTCUSDT',
  baseAsset: 'BTC',
  quoteAsset: 'USDT',
};

// function depthRaiseXRPUSDT(depth: Depth) {
//   console.log(depth);
//   const newDepth: Depth = {
//     ...depth,
//     asks: depth.asks.map((item) => [(Number(item[1]) * 0.05).toString(), item[1]]),
//     bids: depth.bids.map((item) => [(Number(item[1]) * 0.05).toString(), item[1]]),
//   };
//
//   return newDepth;
// }

export const useBinanceService = (): UseBinanceService => {
  const [tickers, setTickers] = useState<Ticker[]>([]);
  const [depth, setDepth] = useState<Depth>(DEPTH_DEFAULT);
  const [trades, setTrades] = useState<Trade[]>([]);
  const [activeCoin, setActiveCoin] = useState<string>('BTCUSDT');
  const [symbolInfo, setSymbolInfo] = useState<SymbolInfo>(SYMBOL_INFO_DEFAULT);

  useEffect(() => {
    getTickers();
    getDepth();
    getTrades();
  }, [activeCoin]);

  const getTickers = async () => {
    const data: ResponseTicker = await api.get('binance/ticker');
    setTickers(data.tickers);
    getSymbolInfo();
  };

  const getDepth = async () => {
    const data: ResponseDepth = await api.post('binance/depth', {symbol: activeCoin});
    // const raiseXRPUSDTdepth = depthRaiseXRPUSDT(data.depth);
    setDepth(data.depth);
  };

  const getTrades = async () => {
    const data: ResponseTrade = await api.post('binance/trades', {symbol: activeCoin});
    setTrades(data.trades);
  };

  const getSymbolInfo = async () => {
    const data: {success: number; symbolInfo: SymbolInfo} = await api.get('binance/symbol-info?symbol=' + activeCoin);
    setSymbolInfo(data.symbolInfo);
  };

  return {
    tickers,
    getTickers,
    depth,
    getDepth,
    trades,
    getTrades,
    activeCoin,
    onChangeActiveCoin: setActiveCoin,
    symbolInfo,
    getSymbolInfo,
  };
};
