import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';

import {ElevationTable} from '../elevation-table/elevation-table';
import {ElevationTableHead} from '../elevation-table/elevaton-head';
import {ElevationTableHeadCell} from '../elevation-table/elevaton-head-cell';
import {ElevationTableBody} from '../elevation-table/elevation-body';
import {ElevationTableRow} from '../elevation-table/elevation-row';
import {ElevationTableCell} from '../elevation-table/elevatoin-cell';
// import {ElevationTableCaption} from '../elevation-table/elevation-caption';
import './elevation.scss';

const b = bem('elevation');

type Props = {
  list: {a: string; b: string; c: string; z?: boolean}[];
  className?: string;
  labelA: string;
  labelB: string;
  labelC: string;
  onChange?: (symbol: string) => void;
  tableHeader?: string;
};

export const Elevation = ({list, className, labelA, labelB, labelC, tableHeader, onChange}: Props) => {
  const cls = cn(b(), className);

  const handleChange = (symbol: string) => {
    if (onChange) onChange(symbol);
  };

  const getStatusValid = (valueC: string, valueZ?: boolean) => {
    if (valueZ === undefined) return Number(valueC) > 0;
    return undefined;
  };

  const getStatusValidTrade = (value?: boolean) => {
    if (value === undefined) return undefined;
    return value;
  };

  return (
    <div className={cls}>
      <ElevationTable>
        {/* {tableHeader && <ElevationTableCaption>{tableHeader}</ElevationTableCaption>}*/}
        <ElevationTableHead>
          <ElevationTableRow>
            <ElevationTableHeadCell className={b('head-cell')}>{labelA}</ElevationTableHeadCell>
            <ElevationTableHeadCell className={b('head-cell')}>{labelB}</ElevationTableHeadCell>
            <ElevationTableHeadCell className={b('head-cell')}>{labelC}</ElevationTableHeadCell>
          </ElevationTableRow>
        </ElevationTableHead>
        <ElevationTableBody>
          {list.map(({a, b, c, z}, index) => {
            // @ts-ignore
            return (
              <ElevationTableRow key={index} onClick={() => handleChange(a)}>
                <ElevationTableCell valid={getStatusValidTrade(z)}>{a}</ElevationTableCell>
                <ElevationTableCell>{b}</ElevationTableCell>
                <ElevationTableCell valid={getStatusValid(c, z)}>
                  {c}
                  {z !== undefined ? '' : '%'}
                </ElevationTableCell>
              </ElevationTableRow>
            );
          })}
        </ElevationTableBody>
      </ElevationTable>
    </div>
  );
};
