import React, {useState} from 'react';
import dayjs from 'dayjs';

import {Row, Cell} from 'shared/ui/tables';

import {Button} from '../../shared/ui';
import {bem} from '../../shared/bem';
import {DialogModal} from '../dialog-modal';
import {DepositData} from '../../services/user/types';

const b = bem('transaction-admin-table');

type Props = {
  deposit: DepositData;
  onUpdateDepositStatus: (
    id: string,
    status: 'Failed' | 'Success',
    userId: string,
    amount: string,
    coin: string,
  ) => void;
};

export function DepositAdminTableItem({deposit, onUpdateDepositStatus}: Props) {
  const {amount, coin, date, network, status, address, userId, _id} = deposit;
  const [openModal, setOpenModal] = useState(false);

  const handleSuccess = () => {
    if (userId && _id) onUpdateDepositStatus(_id, 'Success', userId, amount, coin);
  };

  const handleFailed = () => {
    if (userId && _id) onUpdateDepositStatus(_id, 'Failed', userId, amount, coin);
  };

  return (
    <>
      <Row>
        <Cell>{_id}</Cell>
        <Cell>{userId}</Cell>
        <Cell>{coin}</Cell>
        <Cell>{dayjs(date).format('HH:mm:ss DD.MM.YYYY')}</Cell>
        <Cell>{amount}</Cell>
        <Cell>{network}</Cell>
        <Cell>{address}</Cell>
        <Cell>{status}</Cell>
        <Cell>
          <Button className={b('btn')} onClick={() => setOpenModal(true)}>
            Edit
          </Button>
        </Cell>
      </Row>
      {openModal && (
        <DialogModal
          onFailed={handleFailed}
          onSuccess={handleSuccess}
          onClose={() => setOpenModal(false)}
          label={'onfirm the deposit?'}
        />
      )}
    </>
  );
}
