import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';

import {DepositData} from '../../services/user/types';
import {TransactionTableItem} from './transaction-table-item';
import {TableContentBlank} from '../table-content-blank';

import './transaction-table.scss';

const b = bem('transaction-table');

type Props = {
  operations: DepositData[]; // deposits & withdrawers
  tableName: string;
};

export const TransactionTable = ({operations, tableName}: Props) => {
  return (
    <div className={b()}>
      <Tables.Table>
        <Tables.Caption>{tableName}</Tables.Caption>
        {operations.length ? (
          <>
            <Tables.Head>
              <Tables.Row>
                <Tables.HeadCell>Coin</Tables.HeadCell>
                <Tables.HeadCell>Date</Tables.HeadCell>
                <Tables.HeadCell>Amount</Tables.HeadCell>
                <Tables.HeadCell>Network</Tables.HeadCell>
                <Tables.HeadCell>Address</Tables.HeadCell>
                <Tables.HeadCell>Status</Tables.HeadCell>
              </Tables.Row>
            </Tables.Head>
            <Tables.Body>
              {operations.map((item, index) => (
                <TransactionTableItem key={`${item.date}-${index}`} deposit={item} />
              ))}
            </Tables.Body>
          </>
        ) : (
          <Tables.Body>
            <Tables.Row>
              <Tables.Cell>
                <TableContentBlank message={'No deposit requests'} />
              </Tables.Cell>
            </Tables.Row>
          </Tables.Body>
        )}
      </Tables.Table>
    </div>
  );
};
