import React from 'react';
import {useNavigate} from 'react-router-dom';

import {bem} from 'shared/bem';
import {Row, Cell} from 'shared/ui/tables';
import {Chat} from 'services/admin/types';

import './support-table.scss';

const b = bem('support-table');

type Props = {
  chat: Chat;
};

export function SupportChatItem({chat}: Props) {
  const navigate = useNavigate();
  const {userId, _id, messages} = chat;

  const handleRedirect = () => {
    navigate(`/support/chat?id=${_id}`);
  };

  return (
    <Row onClick={handleRedirect}>
      <Cell className={b('cell')}>{_id}</Cell>
      <Cell className={b('cell')}>{userId}</Cell>
      <Cell className={b('cell')}>{messages.length}</Cell>
    </Row>
  );
}
