import React, {useEffect} from 'react';
import dayjs from 'dayjs';

import {useBinanceService} from 'services/binance';
import {Elevation, ElevationOrderBook, ElevationChart, ElevationTrade} from 'components/trading/elevation';
import {bem} from 'shared/bem';

import './trading.scss';

const b = bem('trading');

export const Trading = () => {
  const {tickers, getTickers, depth, getDepth, trades, getTrades, activeCoin, onChangeActiveCoin, symbolInfo} =
    useBinanceService();

  useEffect(() => {
    getTickers();
    getDepth();
    getTrades();
  }, []);

  const mapTickers = tickers.map((item) => ({a: item.symbol, b: item.highPrice, c: item.priceChangePercent}));

  const mapTrades = trades.map((item) => ({
    a: item.price,
    b: item.qty,
    c: dayjs(item.time).format('HH:mm:ss'),
    z: item.isBuyerMaker,
  }));

  const priceBid = depth.asks.length ? depth.bids[0][0] : '0';
  const priceAsk = depth.asks.length ? depth.asks[0][0] : '0';
  return (
    <div className={b('wrapper-container')}>
      <div className={b()}>
        <Elevation
          onChange={onChangeActiveCoin}
          list={mapTickers}
          className={b('symbols')}
          labelA={'Pairs'}
          labelB={'Price'}
          labelC={'Change'}
        />
        <div className={b('wrapper')}>
          <ElevationChart symbol={activeCoin} className={b('chart')} />
          <div className={b('wrapper-group')}>
            <ElevationTrade price={priceAsk} symbolInfo={symbolInfo} type={'BUY'} className={b('trade')} />
            <ElevationTrade price={priceBid} symbolInfo={symbolInfo} type={'SELL'} className={b('trade')} />
          </div>
        </div>
        <div className={b('wrapper-group-sidebar')}>
          <ElevationOrderBook
            className={b('order-book')}
            bids={depth.bids}
            asks={depth.asks}
            labelA={'Price'}
            labelB={'Quantity'}
            tableHeader={'Order Book'}
            activeCoin={activeCoin}
          />
          <Elevation
            tableHeader={'Market trades'}
            list={mapTrades}
            className={b('symbols')}
            labelA={'Price'}
            labelB={'Quantity'}
            labelC={'Time'}
          />
        </div>
      </div>
    </div>
  );
};
