import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';

import './elevatoin-table.scss';

type ElementProps = React.HTMLProps<HTMLTableElement>;

type Props = {
  className?: string;
} & ElementProps;

const b = bem('elevation-table');

export const ElevationTable = ({className, ...rest}: Props) => {
  const cls = cn(b('table'), className);
  return <table border={0} className={cls} {...rest} />;
};
