import React from 'react';
import {useNavigate} from 'react-router-dom';

import {useAuth} from 'services/auth';
import {bem} from 'shared/bem';

import {navItems} from './consts';
import {NavbarItem} from './navbar-item';
import {NavbarUser} from './navbar-user';
import './navbar.scss';

export const b = bem('navbar');

export function Navbar() {
  const navigate = useNavigate();
  const {isLoggedIn} = useAuth();

  const handleRedirectToMain = () => {
    navigate('/');
  };

  const filterNavItems = navItems.filter(({link}) => link === '/trading' || link === '/deposit');

  return (
    <div className={b({authenticated: isLoggedIn})}>
      <div onClick={handleRedirectToMain} className={b('logo', {authenticated: isLoggedIn})}>
        adavasta
      </div>
      <div className={b('inner')}>
        {filterNavItems.map((item) => (
          <NavbarItem key={item.link} item={item} />
        ))}
      </div>
      <NavbarUser />
    </div>
  );
}
