import React from 'react';
import {Link, useNavigate} from 'react-router-dom';

import {useAuth} from 'services/auth';
import {useUser} from 'services/user';
import {bem} from 'shared/bem';

import './extend-area.scss';

const b = bem('extend-area');

export const ExtendArea = () => {
  const {user} = useUser();
  const {logOut} = useAuth();
  const navigate = useNavigate();

  const handleLogOut = async () => {
    try {
      await logOut();
    } finally {
      navigate('/');
    }
  };

  return (
    <ul className={b()}>
      <li className={b('header')}>
        <Link className={b('link')} to={'/profile'}>
          {user?.username}
        </Link>
      </li>
      <p className={b('line')} />
      <li>
        <Link to={'profile'} className={b('link')}>
          Overview
        </Link>
      </li>
      <li>
        <Link to={'/support'} className={b('link')}>
          Support
        </Link>
      </li>
      <li>
        <Link to={'/verification'} className={b('link')}>
          Verification
        </Link>
      </li>
      <p className={b('line')} />
      <li className={b('footer')} onClick={handleLogOut}>
        Logout
      </li>
    </ul>
  );
};
