import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';

import {DepositAdminTableItem} from './transaction-admin-table-item';
import {TableContentBlank} from '../table-content-blank';
import {DepositData} from '../../services/user/types';

import './transaction-admin-table.scss';

const b = bem('transaction-admin-table');

type Props = {
  operations: DepositData[];
  updateOperation: (id: string, status: 'Success' | 'Failed', userId: string, amount: string, coin: string) => void;
};

export const TransactionAdminTable = ({operations, updateOperation}: Props) => {
  return (
    <>
      <div className={b()}>
        <Tables.Table>
          <Tables.Caption>Deposits requests</Tables.Caption>
          {operations.length ? (
            <>
              <Tables.Head>
                <Tables.Row>
                  <Tables.HeadCell>Order ID</Tables.HeadCell>
                  <Tables.HeadCell>User ID</Tables.HeadCell>
                  <Tables.HeadCell>Coin</Tables.HeadCell>
                  <Tables.HeadCell>Date</Tables.HeadCell>
                  <Tables.HeadCell>Amount</Tables.HeadCell>
                  <Tables.HeadCell>Network</Tables.HeadCell>
                  <Tables.HeadCell>Address</Tables.HeadCell>
                  <Tables.HeadCell>Status</Tables.HeadCell>
                </Tables.Row>
              </Tables.Head>
              <Tables.Body>
                {operations.map((item, index) => (
                  <DepositAdminTableItem
                    key={`${item.date}-${index}`}
                    deposit={item}
                    onUpdateDepositStatus={updateOperation}
                  />
                ))}
              </Tables.Body>
            </>
          ) : (
            <Tables.Body>
              <Tables.Row>
                <Tables.Cell>
                  <TableContentBlank message={'No deposit requests'} />
                </Tables.Cell>
              </Tables.Row>
            </Tables.Body>
          )}
        </Tables.Table>
      </div>
    </>
  );
};
