import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';
import {Chat} from 'services/admin/types';
import {TableContentBlank} from 'components/table-content-blank';

import {SupportChatItem} from './support-chat-item';
import './support-table.scss';

const b = bem('support-table');

type Props = {
  chats: Chat[];
};

export const SupportChatTable = ({chats}: Props) => {
  return (
    <Tables.Table className={b('table')}>
      <Tables.Caption className={b('caption')}>Chats</Tables.Caption>
      {chats.length ? (
        <>
          <Tables.Head>
            <Tables.Row>
              <Tables.HeadCell className={b('head-cell')}>Chat ID</Tables.HeadCell>
              <Tables.HeadCell className={b('head-cell')}>User ID</Tables.HeadCell>
              <Tables.HeadCell className={b('head-cell')}>Count</Tables.HeadCell>
            </Tables.Row>
          </Tables.Head>
          <Tables.Body>
            {chats.map((item) => (
              <SupportChatItem key={item._id} chat={item} />
            ))}
          </Tables.Body>
        </>
      ) : (
        <Tables.Body>
          <Tables.Row>
            <Tables.Cell>
              <TableContentBlank message={'No chats'} />
            </Tables.Cell>
          </Tables.Row>
        </Tables.Body>
      )}
    </Tables.Table>
  );
};
