import React, {useCallback, useState} from 'react';

import {Button, Form, FormGroup, Input, Label, WarnIcon} from 'shared/ui';
import {bem} from 'shared/bem';
import {useUser} from 'services/user';
import {DepositData, Network} from 'services/user/types';
import {SelectOptions} from 'components/select-modal/select-options';

import './withdraw.scss';
import {TransactionTable} from '../../transaction-table';

const b = bem('withdraw');

export const Withdraw = () => {
  const {
    user,
    sendWithdraw,
    dictionary: {networks, coins},
  } = useUser();

  const [currency, setCurrency] = useState('BTC');
  const [isOpenModal, setOpenModal] = useState<boolean>(false);

  const [network, setNetwork] = useState('');
  const [isOpenModalNet, setOpenModalNet] = useState<boolean>(false);

  const [amount, setAmount] = useState('');
  const [address, setAdress] = useState('');

  const handleChangeAmount = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setAmount(e.target.value);
  }, []);

  const handleChangeAddress = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setAdress(e.target.value);
  }, []);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    const depositData: DepositData = {
      network,
      coin: currency,
      amount,
      address,
    };

    sendWithdraw(depositData);
  };

  const canSubmit = Boolean(currency.trim()) && Boolean(network.trim()) && Boolean(amount.trim());

  const filtersSuccessNetwork = networks.filter((item: Network) => item.link?.includes(currency));

  return (
    <>
      <div className={b()}>
        <h5 className={b('header')}>Withdraw</h5>
        <Form className={b('form')} onSubmit={handleSubmit}>
          <FormGroup className={b('form-group')}>
            <Label htmlFor={'currency'} className={b('label')}>
              Currency
            </Label>
            <Input
              className={b('input')}
              onClick={() => setOpenModal(true)}
              readOnly={true}
              id={'currency'}
              value={currency}
              placeholder={'Select currency'}
            />
          </FormGroup>
          <FormGroup className={b('form-group')}>
            <Label htmlFor={'network'} className={b('label')}>
              Network
            </Label>
            <Input
              className={b('input')}
              onClick={() => setOpenModalNet(true)}
              readOnly={true}
              id={'currency'}
              value={network}
              placeholder={'Select Network'}
            />
          </FormGroup>
          <div className={b('warn')}>
            <div className={b('warn-logo')}>
              <WarnIcon />
            </div>
            <div className={b('warn-text')}>
              Ensure the network you choose to withdraw matches the withdrawal network, or assets may be lost.
            </div>
          </div>
          <FormGroup className={b('form-group')}>
            <Label htmlFor={'amount'} className={b('label')}>
              Amount
            </Label>
            <Input
              className={b('input')}
              id={'amount'}
              value={amount}
              onChange={handleChangeAmount}
              placeholder={'0.00'}
              type={'number'}
            />
          </FormGroup>

          <FormGroup className={b('form-group')}>
            <Label htmlFor={'address'} className={b('label')}>
              Amount
            </Label>
            <Input
              className={b('input')}
              id={'address'}
              value={address}
              onChange={handleChangeAddress}
              placeholder={'You external address'}
            />
          </FormGroup>
          <FormGroup className={b('form-group')}>
            <Button
              onClick={handleSubmit}
              type={'submit'}
              kind={'primary'}
              disabled={!canSubmit}
              className={b('btn-submit')}
            >
              Withdraw
            </Button>
          </FormGroup>
        </Form>
      </div>

      <TransactionTable operations={user?.withdraws || []} tableName={'Withdraw requests'} />

      {isOpenModal && (
        <SelectOptions label={'Currency'} options={coins} onClose={() => setOpenModal(false)} onSelect={setCurrency} />
      )}
      {isOpenModalNet && (
        <SelectOptions
          label={'Network'}
          options={filtersSuccessNetwork}
          onClose={() => setOpenModalNet(false)}
          onSelect={setNetwork}
        />
      )}
    </>
  );
};
