import React from 'react';
import {useNavigate} from 'react-router-dom';

import {User} from 'services/user/types';
import {Cell, Row} from 'shared/ui/tables';
import {bem} from 'shared/bem';

import './users.scss';

const b = bem('users');

type Props = {
  user: User;
};

export const UserItem = ({user}: Props) => {
  const navigate = useNavigate();
  const {id, role, username, verified, referrals} = user;

  const handleRedirectToUser = () => {
    navigate(`/user-profile?id=${id}`);
  };

  return (
    <Row onClick={handleRedirectToUser} className={b('row')}>
      <Cell>{id}</Cell>
      <Cell>{username}</Cell>
      <Cell>{role}</Cell>
      <Cell>{verified ? 'TRUE' : 'FALSE'}</Cell>
      <Cell>{referrals.length}</Cell>
    </Row>
  );
};
