import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';

import './elevatoin-table.scss';

type Props = React.HTMLProps<HTMLTableHeaderCellElement> & {
  className?: string;
};

const b = bem('elevation-table');

export const ElevationTableHeadCell = ({className, ...rest}: Props) => {
  const cls = cn(b('head-cell'), className);
  return <th className={cls} {...rest} />;
};
