import React from 'react';

import {bem} from 'shared/bem';

import './select-modal.scss';

const b = bem('select-modal');

type Props = {
  onSelect: (value: string) => void;
  onClose: () => void;
  label: string;
  value: string;
  icon?: React.ReactNode;
};

export const SelectModalItem = ({label, onSelect, onClose, value, icon: Icon}: Props) => {
  const handleSelect = () => {
    onSelect(value);
    onClose();
  };

  // @ts-ignore
  const coinIcon = Icon ? <Icon className={b('icon')} /> : null;
  return (
    <div className={b('option')} onClick={handleSelect}>
      {coinIcon}
      {label}
    </div>
  );
};
