import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';
import {BidsAsks} from 'services/binance/types';

import {ElevationTable} from '../elevation-table/elevation-table';
import {ElevationTableHead} from '../elevation-table/elevaton-head';
import {ElevationTableHeadCell} from '../elevation-table/elevaton-head-cell';
import {ElevationTableBody} from '../elevation-table/elevation-body';
import {ElevationTableRow} from '../elevation-table/elevation-row';
import {ElevationTableCell} from '../elevation-table/elevatoin-cell';
import './elevation.scss';

const b = bem('elevation');

type Props = {
  className?: string;
  bids: BidsAsks[];
  asks: BidsAsks[];
  labelA: string;
  labelB: string;
  tableHeader?: string;
  activeCoin: string;
};

export const ElevationOrderBook = ({asks, bids, className, labelA, labelB, tableHeader, activeCoin}: Props) => {
  const cls = cn(b(), className);

  return (
    <div className={cls}>
      <ElevationTable>
        <ElevationTableHead>
          <ElevationTableRow>
            <ElevationTableHeadCell className={b('head-cell')}>{labelA}</ElevationTableHeadCell>
            <ElevationTableHeadCell className={b('head-cell')}>{labelB}</ElevationTableHeadCell>
          </ElevationTableRow>
        </ElevationTableHead>
        <ElevationTableBody>
          {asks.map((item, index) => {
            return (
              <ElevationTableRow key={index}>
                <ElevationTableCell valid={false}>{item[0]}</ElevationTableCell>
                <ElevationTableCell>{item[1]}</ElevationTableCell>
              </ElevationTableRow>
            );
          })}
        </ElevationTableBody>
      </ElevationTable>

      {bids.length > 0 && <Price coin={activeCoin} price={bids[0][0]} />}

      <ElevationTable>
        <ElevationTableHead>
          <ElevationTableRow>
            <ElevationTableHeadCell className={b('head-cell')}>{labelA}</ElevationTableHeadCell>
            <ElevationTableHeadCell className={b('head-cell')}>{labelB}</ElevationTableHeadCell>
          </ElevationTableRow>
        </ElevationTableHead>
        <ElevationTableBody>
          {bids.map((item, index) => {
            return (
              <ElevationTableRow key={index}>
                <ElevationTableCell valid={true}>{item[0]}</ElevationTableCell>
                <ElevationTableCell>{item[1]}</ElevationTableCell>
              </ElevationTableRow>
            );
          })}
        </ElevationTableBody>
      </ElevationTable>
    </div>
  );
};

const Price = ({price, coin}: {price: string; coin: string}) => {
  const newPrice = coin === 'XRPUSDT' ? (Number(price) * 1.05).toFixed(6) : price;

  return <div className={b('price')}>{newPrice}</div>;
};
