import React from 'react';
import cn from 'classnames';

import {b} from './consts';

import './form.scss';

type ElementProps = React.HTMLProps<HTMLFormElement>;

type OwnProps = {
  inline?: boolean;
  className?: string;
};

type Props = ElementProps & OwnProps;

export function Form({inline, className, ...props}: Props) {
  const cls = cn(b({inline}), className);
  return <form className={cls} {...props} />;
}
