import React, {useCallback, useEffect, useState} from 'react';
import {useSearchParams} from 'react-router-dom';

import {bem} from 'shared/bem';
import {Form} from 'shared/ui/form';
import {Button, CircleIcon, Input} from 'shared/ui';
import {useUser} from 'services/user';
import {Message} from 'services/chat/types';
import {useChatService} from 'services/chat';
import {useAdminService} from 'services/admin';

import {AdminMessageItem} from './admin-message-item';
import './admin-support-chat.scss';

const b = bem('support');

export const AdminSupportChat = () => {
  const {user} = useUser();
  const {sendMessage} = useChatService();
  const {chats, getChats} = useAdminService();

  const [searchParams] = useSearchParams();
  const chatId = searchParams.get('id');
  const chat = chats.find((item) => item._id === chatId);

  const [msg, setMsg] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (chat?.userId) {
      const sendData: Message = {
        message: msg,
        userId: chat?.userId,
        date: new Date().toString(),
      };

      sendMessage(sendData, true);
      setMsg('');
      getChats();
    }
  };

  const handleChangeMsg = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setMsg(e.target.value);
  }, []);

  useEffect(() => {
    getChats();
  }, []);

  useEffect(() => {
    const element = document.getElementsByClassName(b('message-list'))[0];
    element.scrollTop = element.scrollHeight;
  }, [chat?.messages]);

  return (
    <div className={b()}>
      <div className={b('header')}>
        <CircleIcon />
        <span className={b('header-bold')}>Support Online</span>
        <span>Estimated response time 2-4 hours.</span>
      </div>
      <div className={b('chat-box')}>
        <div className={b('message-list')}>
          {chat?.messages.map((item, index) => (
            <AdminMessageItem key={index} message={item} userId={user?.id} />
          ))}
        </div>
        <Form className={b('form')} onSubmit={handleSubmit}>
          <Input className={b('input')} onChange={handleChangeMsg} value={msg} placeholder={'Type message...'} />
          <Button type={'submit'} kind={'primary'} className={b('btn-submit')}>
            Send
          </Button>
        </Form>
      </div>
    </div>
  );
};
