import React from 'react';

import {ReactComponent as BtcIcon} from 'assets/coins/btc.svg';
import {ReactComponent as BchIcon} from 'assets/coins/bch.svg';
import {ReactComponent as AdaIcon} from 'assets/coins/ada.svg';
import {ReactComponent as BnbIcon} from 'assets/coins/bnb.svg';
import {ReactComponent as DotIcon} from 'assets/coins/dot.svg';
import {ReactComponent as EthIcon} from 'assets/coins/eth.svg';
import {ReactComponent as BusdIcon} from 'assets/coins/busd.svg';
import {ReactComponent as LtcIcon} from 'assets/coins/ltc.svg';
import {ReactComponent as TrxIcon} from 'assets/coins/trx.svg';
import {ReactComponent as UsdcIcon} from 'assets/coins/usdc.svg';
import {ReactComponent as UsdtIcon} from 'assets/coins/usdt.svg';
import {ReactComponent as XlmIcon} from 'assets/coins/xlm.svg';
import {ReactComponent as XrpIcon} from 'assets/coins/xrp.svg';

export function getIcon(label: string): React.ReactElement {
  switch (label) {
    case 'BTC':
    case 'Bitcoin':
      return BtcIcon;
    case 'BCH':
    case 'Bitcoin Cash':
      return BchIcon;
    case 'ADA':
    case 'Cardano':
      return AdaIcon;
    case 'BNB':
    case 'Binance Coin':
      return BnbIcon;
    case 'DOT':
    case 'Polkadot':
      return DotIcon;
    case 'ETH':
    case 'Ethereum':
      return EthIcon;
    case 'BUSD':
      return BusdIcon;
    case 'LTC':
    case 'Litecoin':
      return LtcIcon;
    case 'TRX':
    case 'Tron':
      return TrxIcon;
    case 'USDC':
    case 'USD Coin':
      return UsdcIcon;
    case 'USDT':
    case 'Tether':
      return UsdtIcon;
    case 'XLM':
    case 'Stellar':
      return XlmIcon;
    case 'XRP':
    case 'Ripple':
      return XrpIcon;
  }

  return BtcIcon;
}
