import React from 'react';

import {Modal, ModalBody, ModalHeader} from 'shared/ui/modal';
import {bem} from 'shared/bem';
import {ReactComponent as CrossIcon} from 'assets/cross.svg';

import {Button} from '../../shared/ui';
import './dialog-modal.scss';

const b = bem('dialog-modal');

type Props = {
  label: string;
  onClose: () => void;
  onSuccess: () => void;
  onFailed: () => void;
};

export const DialogModal = ({onClose, label, onFailed, onSuccess}: Props) => {
  const handleOnSuccess = () => {
    onSuccess();
    onClose();
  };

  const handleOnFailed = () => {
    onFailed();
    onClose();
  };

  return (
    <Modal onClose={onClose} classNameContent={b('modal')} side={true}>
      <ModalHeader className={b('modal-header')}>
        <CrossIcon onClick={onClose} className={b('cross-icon')} />
      </ModalHeader>
      <ModalBody>
        <div>{label}</div>
        <div className={b('btn-group')}>
          <Button onClick={handleOnSuccess}>Success</Button>
          <Button onClick={handleOnFailed}>Failed</Button>
        </div>
      </ModalBody>
    </Modal>
  );
};
