import React, {useCallback, useEffect, useState} from 'react';
import {useLocation, useNavigate, useSearchParams} from 'react-router-dom';

import {useAuth} from 'services/auth';
import {Button, Form, FormGroup, Input, Label, LockIcon} from 'shared/ui';
import {bem} from 'shared/bem';

import './auth.scss';

const b = bem('auth');

export const Auth = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const {pathname} = useLocation();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const {login, signUp, isLoggedIn} = useAuth();

  const isSignUp = location.pathname.match('signup');

  const handleChangeName = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setUsername(e.target.value);
  }, []);

  const handleChangePassword = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setPassword(e.target.value);
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const referral = searchParams.get('ref');

    try {
      if (pathname === '/login') await login(username, password);
      else if (pathname === '/signup') await signUp(username, password, referral);
    } finally {
      // console.log('isLoggedIn', isLoggedIn);
    }
  };

  useEffect(() => {
    if (isLoggedIn) navigate('/profile');
  }, [isLoggedIn]);

  const handleRedirect = () => {
    const path = isSignUp ? '/login' : '/signup';
    navigate(path);
  };

  const canSubmit = Boolean(username.trim()) && Boolean(password.trim());

  return (
    <div className={b()}>
      <div className={b('header')}>{isSignUp ? 'Sign up' : 'Login'}</div>
      <Form className={b('form')} onSubmit={handleSubmit}>
        <div className={b('form-header')}>Please check that you are visiting the correct URL</div>
        <div className={b('mark')}>
          <LockIcon />
          <span className={b('text-safe')}>https://</span>vasta.com
        </div>
        <FormGroup>
          <Label htmlFor={'username'} className={b('label')}>
            Email
          </Label>
          <Input id={'username'} value={username} onChange={handleChangeName} />
        </FormGroup>
        <FormGroup>
          <Label htmlFor={'password'} className={b('label')}>
            Password
          </Label>
          <Input id={'password'} type={'password'} value={password} onChange={handleChangePassword} />
        </FormGroup>
        {isSignUp && (
          <div className={b('promise')}>
            If you use a non-existent email, your account can be blocked. By tapping the Sign up button, I agree to
            Terms of service and Privacy policy
          </div>
        )}
        <FormGroup className={b('btn-group')}>
          <Button
            onClick={handleSubmit}
            type={'submit'}
            kind={'primary'}
            disabled={!canSubmit}
            className={b('btn-submit')}
          >
            {isSignUp ? 'Sign up' : 'Login'}
          </Button>
          <Button onClick={handleRedirect} type={'button'} kind={'primary'} className={b('btn')}>
            {isSignUp ? (
              <span>
                Already have an account? <span className={b('btn-mark')}>Log in</span>
              </span>
            ) : (
              <span>
                Dont have an account yet? <span className={b('btn-mark')}>Sign up</span>
              </span>
            )}
          </Button>
        </FormGroup>
      </Form>
    </div>
  );
};
