import {useState} from 'react';

import api from 'services/http';

import {UserResponse, DepositData, User, Dictionary, ResponseDictionary, Verification} from './types';
import {ResponseSendMessage} from '../chat/types';

export type UpdateBalanceData = {
  baseAsset: string;
  baseValue: string;
  quoteAsset: string;
  quoteValue: string;
};

export type getUserHandler = () => Promise<void>;
export type sendDepositHandler = (deposit: DepositData) => Promise<string | undefined>;
export type sendWithdrawHandler = (deposit: DepositData) => Promise<void>;
export type sendVerificationHandler = (verification: Verification) => Promise<void>;
export type getDictionaryHandler = () => Promise<void>;
export type updateBalanceHandler = (data: UpdateBalanceData) => Promise<void>;

export type UseService = {
  user: User | null;
  dictionary: Dictionary;
  getUser: getUserHandler;
  getDictionary: getDictionaryHandler;
  sendDeposit: sendDepositHandler;
  sendWithdraw: sendWithdrawHandler;
  sendVerification: sendVerificationHandler;
  updateBalance: updateBalanceHandler;
};

const DICTIONARY_DEFAULT = {coins: [], networks: []};

export const useUserService = (): UseService => {
  const [user, setUser] = useState<User | null>(null);
  const [dictionary, setDictionary] = useState<Dictionary>(DICTIONARY_DEFAULT);

  const getUser = async () => {
    const data: UserResponse = await api.get('/user');
    setUser(data.user);
  };

  const sendDeposit = async (depositData: DepositData) => {
    if (user?.id) {
      try {
        const result: ResponseSendMessage = await api.post('/deposit', {...depositData, usernameId: user.id});
        getUser();
        return result.address;
      } catch (e) {
        console.log('error send deposit');
      }
    }
  };

  const sendWithdraw = async (depositData: DepositData) => {
    if (user?.id) {
      try {
        await api.post('/withdraw', {...depositData, usernameId: user.id});
        getUser();
      } catch (e) {
        console.log('error send deposit');
      }
    }
  };

  const getDictionary = async () => {
    const data = await api.get<never, ResponseDictionary>('/dictionary');
    setDictionary(data.dictionary);
  };

  const sendVerification = async (payload: Verification) => {
    await api.post('/verification', payload);
  };

  const updateBalance = async (payload: UpdateBalanceData) => {
    await api.post('/update-balance', payload);
  };

  return {
    user,
    dictionary,
    getUser,
    getDictionary,
    sendDeposit,
    sendWithdraw,
    sendVerification,
    updateBalance,
  };
};
