import React from 'react';

import {bem} from 'shared/bem';
import * as Tables from 'shared/ui/tables';
import {Indexed} from 'shared/types';
import {TableContentBlank} from 'components/table-content-blank';

import {UserProfileBalanceItem} from './user-profile-balance-item';
import './user-profile.scss';

const b = bem('user-profile');

type Props = {
  balances: Indexed[];
};

export const UserProfileBalanceTable = ({balances}: Props) => {
  return (
    <Tables.Table className={b('table')}>
      <Tables.Caption className={b('caption')}>Balances</Tables.Caption>
      {balances.length ? (
        <>
          <Tables.Head>
            <Tables.Row>
              <Tables.HeadCell className={b('head-cell')}>Coin</Tables.HeadCell>
              <Tables.HeadCell className={b('head-cell')}>Available</Tables.HeadCell>
              <Tables.HeadCell className={b('head-cell')}>On Orders</Tables.HeadCell>
            </Tables.Row>
          </Tables.Head>
          <Tables.Body>
            {balances.map((item, index) => (
              <UserProfileBalanceItem key={`${item.date}-${index}`} balance={item} />
            ))}
          </Tables.Body>
        </>
      ) : (
        <Tables.Body>
          <Tables.Row>
            <Tables.Cell>
              <TableContentBlank message={'No balances'} />
            </Tables.Cell>
          </Tables.Row>
        </Tables.Body>
      )}
    </Tables.Table>
  );
};
