import React from 'react';
import cn from 'classnames';

import {bem} from 'shared/bem';

import './elevatoin-table.scss';

type Props = React.HTMLProps<HTMLTableSectionElement> & {
  className?: string;
};

const b = bem('elevation-table');

export const ElevationTableBody = ({className, ...rest}: Props) => {
  const cls = cn(b('body'), className);
  return <tbody className={cls} {...rest} />;
};
