import {useState} from 'react';

import api from '../http';
import {Message, ResponseGetAllMessage, ResponseSendMessage} from './types';

type sendMessageHandler = (payload: Message, admin?: boolean) => Promise<void>;
type getAllMessagesHandler = () => Promise<void>;

type ChatService = {
  messages: Message[];
  sendMessage: sendMessageHandler;
  getAllMessage: getAllMessagesHandler;
};

export const useChatService = (): ChatService => {
  const [messages, setMessages] = useState<Message[]>([]);

  const sendMessage = async (payload: Message, admin?: boolean) => {
    const param = admin ? '?role=admin' : '';
    await api.post<Message, ResponseSendMessage>('/support-chat' + param, payload);
    getAllMessage();
  };

  const getAllMessage = async () => {
    const {messages} = await api.get<never, ResponseGetAllMessage>('/support-chat');

    setMessages(messages);
  };

  return {
    messages,
    sendMessage,
    getAllMessage,
  };
};
