import React, {Context, FC, createContext, useContext} from 'react';

import {UseService, useUserService} from './use-user-service';

const UserContext = createContext<UseService | null>(null);

export const useUser = (): UseService => useContext(UserContext as Context<UseService>);

type Props = {
  children?: React.ReactNode;
};

export const UserProvider: FC<Props> = ({children}: Props) => {
  const authenticationService = useUserService();

  return <UserContext.Provider value={authenticationService}>{children}</UserContext.Provider>;
};
