import React from 'react';

import {Row, Cell} from 'shared/ui/tables';
import {bem} from 'shared/bem';

import './style.scss';
import {getIcon} from '../../shared/utils';

const b = bem('balance-table');

type Props = {
  coin: string;
  value: string;
};

export function BalanceTableItem({coin, value}: Props) {
  const Icon = getIcon(coin);
  // @ts-ignore
  const coinIcon = <Icon className={b('icon')} />;
  return (
    <Row>
      <Cell className={b('cell')}>
        {coinIcon}
        {coin}
      </Cell>
      <Cell>{value}</Cell>
      <Cell>{value}</Cell>
    </Row>
  );
}
