import React, {useEffect} from 'react';

import {useAdminService} from 'services/admin';

import {SupportChatTable} from './support-chat-table';

import './support.scss';

export const Support = () => {
  const {chats, getChats} = useAdminService();

  useEffect(() => {
    getChats();
  }, []);

  return <SupportChatTable chats={chats || []} />;
};
