import React from 'react';
import cn from 'classnames';
// @ts-ignore
import TradingViewWidget, {Themes} from 'react-tradingview-widget';

import {bem} from 'shared/bem';

import './elevation.scss';

const b = bem('elevation');

type Props = {
  className?: string;
  symbol: string;
};

export const ElevationChart = ({className, symbol}: Props) => {
  const cls = cn(b(), className);
  const symbolProp = `BINANCE:${symbol}`;

  return (
    <div className={cls}>
      <TradingViewWidget symbol={symbolProp} hide_top_toolbar={true} theme={Themes.DARK} locale="en" autosize />
    </div>
  );
};
